/** Actions au niveau d'un atelier. */
export abstract class WspAction<E extends IWspActionCtx = IRegPointer<IWspUiEnv>> extends ChainAction<E> {
	isVisible(ctx: E): boolean {
		if (!ctx.reg?.env.wsp) return false;
		return super.isVisible(ctx);
	}
}

/**
 * Contexte des actions associÃ©es Ã  plusieurs atelier
 */
export interface IWspsActionCtx extends IRegsPointer<IWspUiEnv> {}

export abstract class WspsAction<E extends IWspsActionCtx | IWspActionCtx> extends Action<E> {
	/** action affichÃ©e QUE dans les contextes ODB **/
	isBackendDb: boolean;
	/** action affichÃ©e QUE dans les contextes FS **/
	isBackendFs: boolean;
	/** */
	checkCompatKey: string | null;
	/** L'action n'est visible QUE dans le contexte IWspsActionCtx */
	onlyOnWsps: boolean = true;

	/** Liste des ateliers Ã  traiter
	 * 	@return null si aucun atelier Ã  traiter */
	public getWspRegs(ctx: E): IReg<IWspUiEnv>[] {
		let regs: IReg<IWspUiEnv>[] = [];
		if ((ctx as IWspsActionCtx).regs)
			(ctx as IWspsActionCtx).regs.forEach((entry) => {if (entry.env.wsp) regs.push(entry)});
		else if (!this.onlyOnWsps && (ctx as IWspActionCtx).reg && (ctx as IWspActionCtx).reg.env.wsp)
			regs.push((ctx as IWspActionCtx).reg)
		return regs.length > 0 ? regs : null;
	}

	isEnabled(ctx: E): boolean {
		if (this.checkCompatKey && !Desk.checkCompat(this.checkCompatKey)) return false;
		return super.isEnabled(ctx);
	}

	isVisible(ctx: E): boolean {
		let regs = this.getWspRegs(ctx);
		if (!regs) return false;
		if (this.isBackendDb && regs.findIndex((reg) => reg.env.wsp.backEnd === "fs") == -1) return false;
		if (this.isBackendFs && regs.findIndex((reg) => reg.env.wsp.backEnd === "odb") == -1) return false;
		return super.isVisible(ctx);
	}

	getDescription(ctx: E): string {
		if (this.checkCompatKey && !Desk.checkCompat(this.checkCompatKey))
			return "Ce navigateur n'est pas compatible avec cette fonction";
		return super.getDescription(ctx);
	}

	checkObjectRootVisiblePerm(ctx: E, perms?: string | string[], and?: boolean): boolean {
		if (!perms) perms = this._visPerms;
		let regs = this.getWspRegs(ctx);
		if (perms && regs && regs.findIndex((reg) => !reg.hasPerm(perms, (reg as ISecurityCtxPointer).securityCtx), and) > -1) return false;
		return true;
	}

	checkObjectRootPerm(ctx: E, perms?: string | string[], and?: boolean): boolean {
		if (!perms) perms = this._enablePerms;
		let regs = this.getWspRegs(ctx);
		if (perms && regs && regs.findIndex((reg) => !reg.hasPerm(perms, (reg as ISecurityCtxPointer).securityCtx), and) > -1) return false;
		return true;
	}

}

/** FS only. */
export class ScanFsUpdatesAction extends WspAction {
	constructor() {
		super();
		this._icon = "/@skin@/commons/icons/refresh.svg";
		this._label = "RafraÃ®chir l'atelier";
		this._description = "DÃ©tecte les changements de l'atelier rÃ©alisÃ©s directement dans le systÃ¨me de fichier.";
	}

	isInstantiable(ctx: IRegPointer<IWspUiEnv>): boolean {
		return ctx.reg.env.universe.config.backEnd === "fs";
	}

	execute(ctx: IRegPointer<IWspUiEnv>, ev?: Event): Promise<void> {
		const progress = POPUP.showProgress(ctx.reg.env.uiRoot, "Recherche des changements...");
		return ctx.reg.env.universe.wspServer.config.adminWspFsUrl.fetchVoid(IO.qs("cdaction", "ScanFsUpdates", "param", ctx.reg.env.wsp.code)).finally(() => progress.close());
	}
}

/**
 * Export d'un atelier (.scwsp)
 */
export class ExportWspAction<E extends IWspActionCtx = IRegPointer<IWspUiEnv>> extends WspAction<E> {

	constructor(id?: string) {
		super(id || "exportWsp");
		this._label = "Exporter une archive de l'atelier (.scwsp)";
		this._group = "importExport";
		this._enablePerms = "read.node.export";
	}

	execute(ctx: E, ev?: Event): any | "noPreventDefault" | "noStopPropag" | "noStopPropag-noPreventDefault" {
		const wsp = ctx.reg.env.wsp;
		const name = IO.getValidFileName(wsp.wspTitle, ".scwsp", 'dateTime');
		const url = ctx.reg.env.universe.config.wsps.exportUrl.resolve(IO.qs("cdaction", "Export", "param", wsp.code, "format", "jar", "scope", "node", "mode", "wspTree", "refUris", "", "downloadFileName", name)).url
		const a = <a href={url} download={name}/> as HTMLAnchorElement;
		a.click();
	}
}

