/** Migration automatique d'un atelier */
export class AutoMigrWspAction extends WspAction {

	constructor(id?: string) {
		super(id || "AautoMigrWsp");
		this._label = "Migrer l'atelier...";
		this._description = "Migrer les contenus de l'atelier";
		this._group = "wsp";
		this.setIcon("/@skin@/wsp/actions/wspMigr.svg");
		this.requireEnabledPerm("action.wsp#migrate");
	}

	isVisible(ctx: IWspActionCtx): boolean {
		if (ctx.reg.env.wsp?.migrating) return false;
		let infoWsp = ctx.reg.env.wsp?.infoWsp || ctx.reg.env.wsp?.infoWspError;
		if (!infoWsp?.isMigrationNeeded) return false;
		return super.isVisible(ctx);
	}

	async execute(ctx: IWspActionCtx, ev?: Event): Promise<void> {
		const reg = ctx.reg;
		const wspTitle = reg.env.wsp.wspTitle;
		const msg = `Voulez-vous exÃ©cuter la migration des contenus de l'atelier '${wspTitle}' ?`;
		if (await POPUP.confirm(<div>
			<div>{msg}</div>
			<div><b>Vous ne pourrez pas annuler cette action.</b></div>
		</div>, reg.env.uiRoot, {okLbl: "Migrer les contenus", cancelLbl: "Annuler"})) {
			try {
				let wspMetaEditorDom: Document = await WSPTYPE.getUpdateEditor(ctx.reg.env.universe.wspServer, ctx.reg.env.wsp.code);
				let vUnknownModels: string[] = [];
				// Construction du wspTypeInst cible (on ne conserve que les noeuds sÃ©lectionnÃ©s non obsolÃ¨tes, ou "newest")
				let wspTypeInst: JWspTypeInst = {wspOptions: []} as JWspTypeInst;
				let wspTypeDef = wspMetaEditorDom.querySelector("wspTypeDef[newest], wspTypeDef[selected]:not([obsolete])");
				if (wspTypeDef) {
					wspTypeInst.wspType = {
						key: wspTypeDef.getAttribute("key"),
						lang: wspTypeDef.getAttribute("lang"),
						version: wspTypeDef.getAttribute("version"),
						title: wspTypeDef.getAttribute("title"),
						uri: wspTypeDef.getAttribute("uri")
					} as JWspType;
					if (wspTypeDef.getAttribute("unknown"))
						vUnknownModels.push(wspTypeInst.wspType.title)
					let wspOptionsDef = wspMetaEditorDom.querySelectorAll("wspOptionDef[newest], wspOptionDef[selected]:not([obsolete])");
					wspOptionsDef.forEach((wspOptionDef) => {
						wspTypeInst.wspOptions.push({
							key: wspOptionDef.getAttribute("key"),
							lang: wspOptionDef.getAttribute("lang"),
							version: wspOptionDef.getAttribute("version"),
							title: wspOptionDef.getAttribute("title"),
							uri: wspOptionDef.getAttribute("uri"),
							unknown: wspOptionDef.getAttribute("unknown")
						} as JWspType);
						if (wspOptionDef.getAttribute("unknown"))
							vUnknownModels.push(wspOptionDef.getAttribute("title"))
					});

					if (vUnknownModels.length) {
						// Refus de migration dont la cible est auto-dÃ©duite si la cible contient des modeles/exts inconnues : migr manuelle explicite par l'auteur
						const unknownList = vUnknownModels.join(", ");
						POPUP.showNotifError(`La migration de l'atelier '${wspTitle}' ne peut avoir lieu car certaines extensions sont actuellement inconnues : "${unknownList}".\nVeuillez choisir le modÃ¨le cible et ses extensions.`, reg.env.uiRoot)
					} else {
						// - ExÃ©cution de la migration
						await reg.env.wsp.updateWspType(wspTypeInst, true);
						//POPUP.showNotifInfo(`La migration de l'atelier '${wspTitle}' a Ã©tÃ© menÃ©e jusqu'Ã  son terme.`, reg.env.uiRoot, {noCloseBtn: true, autoHide: 5000}) FAUX : action asynchrone => en attente des remontÃ©es ws de fin de migration
					}
				} else
					await POPUP.showNotifError("La cible de migration n'a pas pu Ãªtre dÃ©terminÃ©e. Veuillez ajuster le modÃ¨le documentaire de l'atelier via la fenÃªtre d'Ã©dition des propriÃ©tÃ©s.", reg.env.uiRoot, {noCloseBtn: true});
			} catch (e) {
				await ERROR.report(`La migration de l'atelier '${wspTitle}' n'a pas pu aboutir. Veuillez rÃ©essayer ultÃ©rieurement.`, e);
			}
		}

	}

}

