/** Trashed : restauration */
export class RestoreWspsAction extends WspsAction<(IWspsActionCtx | IWspActionCtx) & IRegPointer<IChainEnv>> {
	static SINGLETON = new RestoreWspsAction();

	constructor(id?: string) {
		super(id || "restoreWsps");
		this.setLabel((ctx: (IWspsActionCtx | IWspActionCtx) & IRegPointer<IChainEnv>) => {
			let count = (ctx as IWspsActionCtx).regs ? (ctx as IWspsActionCtx).regs.length : ((ctx as IWspActionCtx).reg ? 1 : 0);
			return count > 1 ? `Restaurer les ${count} ateliers` : "Restaurer l'atelier";
		});
		this._group = "adminWsp";
		this.onlyOnWsps = false;
		this.requireEnabledPerm("action.wsp#restore");
		this.setIcon("/@skin@/wsp/actions/wspRestore.svg");
	}

	/**
	 * @return : false si aucune restauration n'a eu lieu
	 * @param ctx
	 * @param ev
	 */
	async execute(ctx: (IWspsActionCtx | IWspActionCtx) & IRegPointer<IChainEnv>, ev?: Event): Promise<boolean> {
		let wspTitleErrosArray: string[] = [];
		let dialog: IPopupable;
		try {
			let regs = this.getWspRegs(ctx);

			let wsps: Wsp[] = [];
			regs.forEach((entry) => {wsps.push(entry.env.wsp)});
			wsps = WSP.sortWspsByDeep(wsps);
			let count = wsps.length;

			// Controle : si drv/drf, le drfRefWsp ou drvMasterWsp ne doit pas Ãªtre en corbeille
			//(await WSP.listWsps(this.reg.env.universe.wspServer, {inTrash: true, fields: ["srcDt", "srcRoles", "srcRi"]})).wsps
			let mandatWspsTitles: string[] = [];
			if (wsps.find((wsp) => (wsp.infoWsp || wsp.infoWspError).props.drvMasterWsp || (wsp.infoWsp || wsp.infoWspError).props.drfRefWsp ? true : false)) {
				await Promise.all(wsps.map(async (wsp) => {
					let parentWspCd = (wsp.infoWsp || wsp.infoWspError).props.drvMasterWsp || (wsp.infoWsp || wsp.infoWspError).props.drfRefWsp;
					if (parentWspCd) {
						let parentWsp = new Wsp(ctx.reg.env.universe.wspServer, parentWspCd);
						await parentWsp.waitForLoad();
						if (parentWsp.isDeleted && !wsps.find((wsp) => wsp.code === parentWspCd))
							mandatWspsTitles.push(parentWsp.wspTitle)
					}
				}))
			}
			if (mandatWspsTitles.length > 0) {
				let list = mandatWspsTitles.join(", ");
				await ERROR.show(`Les ateliers suivants doivent Ãªtre restaurÃ©s au prÃ©alable : '${list}'.`);
				return false;
			}

			if (await POPUP.confirm(count > 1 ? `Voulez-vous vraiment restaurer les '${count}' ateliers ?` : "Voulez-vous vraiment restaurer l'atelier ?", window.document, {cancelLbl: "Annuler", okLbl: "Restaurer"})) {
				const {WspsMonitoring} = await import("back/wsp/widgets/wsp/wspsMonitoring.js");
				const wspsMonitoring = <WspsMonitoring Ã®={{
					wsps: wsps,
					defaultStateKey: "notStart",
					states: [
						{key: "notStart", level: "disabled", label: "En attente"},
						{key: "toRemove", level: "disabled", label: "Ã restaurer"},
						{key: "pending", level: "pending", label: "En cours..."},
						{key: "restored", level: "success", label: "RestaurÃ©"},
						{key: "failed", level: "error", label: "Ãchec"}
					]
				} as OWspsMonitoringInit}/> as WspsMonitoring;

				dialog = POPUP.showDialog(<div>
					<MsgLabel label="Restauration en cours..."/>
					{wspsMonitoring}
				</div>, window.document, {
					skinOver: "c-wsps-action",
					fixSize: false,
					titleBar: null,
					initWidth: '50%',
				});
				let errorsArray: any[] = [];
				for (const entry in wsps) {
					let wsp = wsps[entry];
					wspsMonitoring.updateWspStatus(wsp.code, "pending");
					try {
						await wsp.restore();
						wspsMonitoring.updateWspStatus(wsp.code, "restored");
						await new Promise(resolve => setTimeout(resolve, 1000));
					} catch (e) {
						wspsMonitoring.updateWspStatus(wsp.code, "failed");
						wspTitleErrosArray.push(wsp.wspTitle);
						errorsArray.push(e);
					}
				}
				await new Promise(resolve => setTimeout(resolve, errorsArray.length > 0 ? 3000 : 1000));
				if (errorsArray.length > 0) throw errorsArray;
				return true;
			}
		} catch (e) {
			let list = wspTitleErrosArray.join(", ");
			await ERROR.report(list ? "Restauration des ateliers en Ã©chec." : `Restauration des ateliers ${list} en Ã©chec.`, e);
			return true;
		} finally {
			if (dialog) dialog.close();
		}
	}

}

